html {
  margin: 0;
  padding: 0;
}

body {
  max-height: 1200px;
  background-color: #ddd;
  grid-template-columns: 14% 86%;
  grid-auto-rows: 1fr;
  margin: 0;
  padding: 0;
  font-family: Oswald, sans serif;
  display: grid;
  position: relative;
}

#logo {
  justify-self: center;
}

.nav a:hover {
  color: #fff;
  text-shadow: 1px 1px 1px #000;
}

.main img {
  min-width: 100%;
  min-height: 100%;
  grid-column: span 1;
}

.navbar {
  height: 100%;
  max-width: 14%;
  color: #000;
  text-align: center;
  background-color: #6b9080;
  grid-column: 1 / span 1;
  grid-template-columns: 1fr;
  grid-auto-rows: max-content;
  padding: 10px;
  font-weight: 800;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
}

.main {
  max-width: 101%;
  background-blend-mode: darken;
  color: #fff;
  background-color: #afafaf;
  background-position: center;
  background-size: cover;
  grid-area: 1 / 2 / span 2 / span 1;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  justify-content: center;
  align-items: center;
  margin-left: -1%;
  font-size: 4em;
  display: grid;
}

.main.home {
  background-image: url("https://images.unsplash.com/photo-1652070090779-c791b9a9b628?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1674&q=80");
}

.main.environmental {
  background-image: url("env2.f2fd1df9.jpg");
  grid-row: span 2;
  display: grid;
}

.section.hero {
  text-align: center;
  text-shadow: 2px 2px 2px #000;
  justify-self: center;
  margin-top: 5%;
  display: grid;
}

.major {
  box-sizing: border-box;
  border-top: 2px solid #afafaf;
  grid-area: 3 / 2 / span 2 / span 1;
  grid-template-columns: repeat(3, 1fr);
  padding: 20px;
  font-size: 1.5em;
  display: grid;
}

.main.home {
  grid-row: 1 / span 3;
}

.major.home {
  grid-row: 4 / span 2;
}

.major.environmental {
  grid-row: 3 / span 5;
  display: grid;
}

.env-serv {
  border-bottom: 5px solid #333;
  margin-top: 30px;
  font-size: 1.25em;
  font-weight: 600;
}

.major.environmental h1 {
  align-self: end;
  margin-left: 15px;
}

.major.environmental p {
  align-self: start;
  line-height: 2.5em;
}

.major.environmental img {
  max-width: 100%;
  margin: 10px;
}

.prefooter {
  grid-column: span 2;
}

.footer {
  border-top: 2px solid #afafaf;
  grid-column: 1 / span 3;
  grid-template-columns: 1fr 1fr;
  margin-top: 20px;
  font-weight: 200;
  display: grid;
}

.footer.one {
  grid-column: 1 / span 1;
  justify-self: start;
}

.footer-two {
  grid-column: 2 / span 1;
  justify-self: end;
}

.footer ul {
  grid-column: span 2;
  grid-template-columns: 1fr 1fr;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
}

.footer li {
  grid-column: span 1;
  margin: 0;
}

.footer h3 {
  margin: 0;
}

.major.about {
  max-width: 100%;
  min-width: 100%;
  box-sizing: border-box;
  grid-row: 3 / span 3;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 25px;
  display: grid;
}

.major.about h1 {
  margin-top: 0;
}

.major.about p {
  grid-column: 1 / span 2;
  margin-top: 8px;
}

.major.about .major-logo {
  grid-column: 3 / span 1;
}

.major.about .footer {
  grid-column: 1 / span 3;
}

.major-logo {
  width: 200px;
  height: 200px;
  filter: sepia(20%);
  grid-column: 3 / span 1;
  justify-self: center;
}

.major p {
  grid-column: 1 / span 2;
  margin-left: 10px;
}

#logo img {
  width: 100px;
  height: 100px;
  filter: sepia(20%);
}

ul {
  width: 100%;
  box-sizing: border-box;
  text-align: start;
  grid-column: 1 / span 1;
  padding: 0;
  list-style-type: none;
  display: grid;
}

.navbar li {
  grid-column: 1 / span 1;
  margin-top: 50px;
  display: grid;
}

.navbar a {
  color: #000;
  font-size: 1em;
  font-weight: 800;
  text-decoration: none;
}

.navbar h1 {
  text-shadow: -1px -1px 1px #fff;
  font-size: 2.5em;
}

.navbar h4 {
  font-size: 1em;
}

.navbar h1, .navbar h4 {
  text-align: center;
  margin: 5px;
}

.main.about {
  background-image: url("https://images.unsplash.com/photo-1603077747884-b458237de736?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1770&q=80");
}

.main.staff {
  color: #fff;
  max-height: 100%;
  background-color: #afafaf;
  background-image: url("https://www.mtu.edu/geo/undergraduate/geo-eng/geo-eng-advisory-board/images/slope-stability-800banner.jpg");
  background-size: cover;
  grid-area: 1 / 2 / span 2 / span 1;
  grid-template-columns: 1fr;
  padding-right: 60px;
  font-size: 4em;
  display: grid;
}

.main.contact {
  background-image: url("bridge.a0e0ee7e.jpg");
  grid-area: 1 / 2 / span 2 / span 1;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  justify-content: center;
  font-size: 2em;
  display: grid;
}

.major.staff {
  grid-row: 3 / span 3;
  grid-template-columns: 1fr;
  grid-auto-rows: 95% 5%;
  margin: 0;
  padding: 20px;
  display: grid;
}

.staff-bios {
  grid-area: 1 / 1 / span 2 / span 3;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr 1fr;
  justify-content: center;
  align-items: center;
  display: grid;
}

.bio {
  text-align: center;
  grid-area: 1 / span 1 / span 1;
  grid-template-columns: 1fr;
  grid-auto-rows: max-content;
  display: grid;
}

.staff-bios p {
  grid-column: 1 / span 3;
}

.staff-bios .major-logo {
  grid-column: 4 / span 1;
}

.major.staff .bio-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  grid-area: 1 / 1 / span 1 / span 1;
  justify-self: center;
}

.expanded-bio {
  width: 100%;
  box-sizing: border-box;
  background-color: #ddd;
  grid-column: 2 / span 3;
  grid-template-columns: 1fr 1fr;
  padding-right: 20px;
  transition: transform 1.5s ease-in-out;
  display: grid;
  position: absolute;
  right: 200%;
}

.expanded-bio p {
  min-width: 100%;
  grid-column: 1 / span 1;
  padding: 10px;
}

.expanded-bio span {
  grid-column: 1 / span 1;
  padding: 10px;
}

.expanded-bio-image {
  max-width: 600px;
  max-height: 600px;
  grid-column: 1 / span 1;
}

.major.staff footer {
  grid-area: 4 / span 4 / span 1;
  display: grid;
}

.contact-info {
  grid-column: 1 / span 1;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

#contact-image {
  max-width: 80%;
  max-height: 80%;
  box-sizing: border-box;
  grid-column: span 1;
  justify-self: center;
}

.contact-text {
  grid-column: 2 / span 1;
  padding: 20px;
}

form {
  min-height: 80%;
  min-width: 100%;
  flex-direction: column;
  grid-area: 1 / 2 / span 2 / span 1;
  align-items: center;
  margin-right: 10px;
  padding: 0;
  display: flex;
}

form input {
  height: 10%;
  width: 100%;
  border-radius: 6px;
}

textarea {
  height: 30%;
  width: 100%;
  border: 2px solid #000;
  border-radius: 6px;
}

.contact.major {
  box-sizing: border-box;
  grid-row: 3 / span 2;
  margin-top: 1px;
}

.contact.major footer {
  grid-row: 4 / span 1;
  margin-top: 0;
}

form button {
  width: 80px;
  height: 40px;
  color: #000;
  background-color: teal;
  border-radius: 6px;
  margin: 10px;
  font-weight: 800;
  box-shadow: 2px 2px 2px #000000b3;
}

button:hover {
  filter: brightness(1.5);
  box-shadow: .5px .5px .5px #0003;
}

.hidden {
  display: none;
}

@media screen and (max-width: 1200px) {
  .main.about, .main.environmental {
    grid-row: 1 / span 1;
    font-size: 3em;
  }

  .main.environmental {
    font-size: 3em;
  }

  .main.home {
    grid-row: 1 / span 2;
    font-size: 3em;
  }

  .major.home {
    grid-row: 3 / span 1;
  }

  .major.about, .major.environmental {
    grid-row: 2 / span 2;
  }

  .major.environmental h1, .major.environmental p, .major.environmental li {
    margin-left: 50px;
  }

  .navbar ul {
    justify-self: start;
  }

  .bio h1 {
    margin: 0;
    font-size: 1.4em;
  }

  .main.contact {
    grid-row: span 3;
  }

  .contact.major {
    grid-row: 4 / span 2;
  }

  .major-logo {
    width: 200px;
    height: 200px;
  }

  .major.staff .bio-image {
    width: 180px;
    height: 180px;
  }
}

@media screen and (max-width: 900px) {
  .navbar li {
    margin-top: 25px;
  }

  .navbar a {
    margin-right: 5px;
    font-size: .75px;
  }

  .navbar h1 {
    text-shadow: -1px -1px 1px #fff;
    font-size: 1.75em;
  }

  .navbar h4 {
    font-size: .9em;
  }

  .major-logo {
    width: 180px;
    height: 180px;
  }

  .major.about .major-logo {
    display: none;
  }

  .major.about p {
    grid-column: 1 / span 4;
  }

  .major.home {
    grid-row: 3 / span 2;
    padding: 25px;
  }

  .main.home {
    font-size: 2em;
  }

  .main.about {
    font-size: 3em;
  }

  .major.staff .bio-image {
    width: 130px;
    height: 130px;
  }

  .bio h1 {
    font-size: 1.25em;
  }

  #logo img {
    width: 90px;
    height: 90px;
  }

  .footer h3 {
    font-size: 1em;
  }

  .contact-info {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr 1fr;
  }

  .contact-image {
    grid-area: 1 / 1 / span 1 / span 1;
  }

  .contact-text {
    grid-area: 2 / 1 / span 1 / span 1;
  }

  .main.contact {
    grid-row: span 2;
  }

  .contact.major {
    grid-row: 3 / span 2;
  }
}

@media screen and (max-width: 650px) {
  .major.home {
    grid-row: 2 / span 1;
  }

  .major.about {
    grid-row: 2 / span 3;
  }

  .major.home .major-logo {
    width: 120px;
    height: 120px;
  }

  .navbar #logo, .navbar #logo img {
    width: 40px;
    height: 40px;
  }

  .navbar {
    text-align: start;
  }

  .navbar li {
    margin-top: 25px;
  }

  .navbar a {
    font-size: .5em;
  }

  .navbar h1 {
    font-size: 1em;
  }

  .navbar h4 {
    font-size: .7em;
  }

  #logo img {
    width: 80px;
    height: 80px;
  }

  .main.home {
    font-size: 1em;
  }

  .major.home {
    font-size: .7em;
  }

  .main.about {
    font-size: 2em;
  }

  .staff-bios {
    grid-row: 1 / span 1;
    grid-template-columns: 50% 50%;
  }

  .bio {
    grid-row: span 1;
  }

  .major.staff .bio-image {
    width: 180px;
    height: 180px;
  }

  .footer h3 {
    font-size: .75em;
  }

  .main.staff {
    font-size: 2.5em;
  }

  .main.staff p {
    margin-left: 50px;
  }

  .major-logo {
    width: 180px;
    height: 180px;
  }

  .main.staff {
    grid-row: span 1;
  }

  .major.staff {
    grid-row: 2 / span 2;
    grid-auto-rows: 90% 10%;
  }

  .major.staff footer {
    grid-row: 2 / span 1;
  }
}

